.homeContainer{
    background-color: rgb(54, 171, 255); 
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    font-size: 1rem;
    color: white;
    margin: 0;
    overflow: auto;
}

.homeContainer__hi{
    padding: 1% 3%;
    text-align: justify;
    font-size: 1.2rem;
    color: white;
}

.homeContainer__email{
    color: black;
    font-size: 1.5rem;
    text-align: center;
}

.homeContainer__contact{
    display: flex;
    justify-content: space-around;
    width: 20vw;
}

.fa-facebook, .fa-github, .fa-whatsapp-square, .fa-linkedin{
    font-size: 3rem;
    color: black;
  
}