.store{
    background-image: url(https://www.emprenderalia.com/wp-content/uploads/Crear-una-tienda-online-en-10-pasos-3-meses-y-sin-inversion.jpg);
    height: 100vh;
    width: 100vw;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    background-color: rgb(14, 146, 240);
}

.store :first-child{
    background-color: cyan;
    opacity: 0.6;
}