.todoapp{
    width: 100vw;
    height: 100vh;
    padding: 10vh 10% 0%;
    display: flex;
    flex-direction: column;
    background-image: url(https://image.freepik.com/foto-gratis/bloc-notas-lapiz-sobre-fondo-tablero-madera_24972-539.jpg);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    background-color: black;
    color: white;
    font-size: 1.2rem;
    font-weight: bold;
 
}

.todoapp__title4{
    background-color: rgba(114, 35, 35, 0.489);
    text-align: center;
}

.todos__parrafo{
    cursor: pointer;
    
}

.group{
    display: flex;
    justify-content: space-between;
    margin: 5px 0;
    
}

.complete{
    text-decoration: line-through;
}

.btn-block{
    width: 100%;
}

.footer{
    position: absolute;
    bottom: 0;
    width: 100vw;
    

    
}