.photoContainer{
    background-color: white;
    height: 100vh;
    display: flex;
    flex-wrap: wrap;
}

.photos__img{
    text-align: center;
    width: 30vw;
    height: 28vh;
    margin: 5px;
    box-shadow: 10px 5px 5px black;   
    font-size: 1.2rem;
}

.photoContainer :nth-child(3){
    width: 30vw;
    height: 28vh;
    margin: 5px;
    box-shadow: 10px 5px 5px black;
    background-color: rgb(14, 146, 240);
    display: flex;
    justify-content: center;
    align-items: center;
}

img{
    width: 100%;
    height: 100%;
}
