.loginscreen{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
  
}

.loginscreen__main{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 10;
    text-align: center;
}

.loginscreen__main--box{ 
    padding: 10px;
    display: flex;
    flex-direction: column;
    box-shadow: 10px 5px 5px black;
    border: 2px solid black;
    color: rgb(0, 0, 0);
    font-size: 2rem;
    margin-bottom: 10%;
}

.loginscreen__main--box:hover button{
    background-color: black;
    color: white;

}

#loginscreen__tsparticles{
    position: absolute;
    opacity: 0.6;
    width: 100vw;
    height: 100vh;
    ;
}

